import styled from 'styled-components'

const AlbumOrArtistImage = styled.img`
  height: 100px;
  width: 100px;
  margin-right: 8px;
  cursor: pointer;
`;

const SongInfoContainer = styled.div`
  display: flex;
  flex-direction: column;
  text-align: left;
  margin-left: 8px;
`

const SuggestrResults = styled.div`
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
`

const SongInfoWrapper = styled.div`
  display: flex;
  flex: 1 1 30%;
  margin-bottom: 12px;
  margin-left: 8px;
`

const ClickableText = styled.span`
  color: #333558;
  text-decoration: underline;
  cursor: pointer;

  :hover {
    opacity: .7;
  }
`

function Results(params) {
  const {loading, recommendations, setActiveStep} = params

  const openInSpotify = (url) => {
    window.open(url, '_blank')
  }

  const openSeedTab = () => {
    setActiveStep(1)
  }
 
  const songInfoDiv = (props, song) => (
    <SongInfoWrapper {...props} id={song.id} key={song.id} >
    <AlbumOrArtistImage src={song?.album?.images?.[0]?.url} onClick={() => openInSpotify(song?.external_urls?.spotify)}/>
    <SongInfoContainer>
      <span>"{song.name}"</span>
      <span>{song.artists?.[0]?.name}</span>
      <span>({song.album?.release_date})</span>
    </SongInfoContainer>
  </SongInfoWrapper>
  )

  return (
    <SuggestrResults className="suggestr-results">
      {recommendations && recommendations.map((song) => songInfoDiv({}, song))}
      {(!loading && (!recommendations || !recommendations.length)) && (<p>No results yet. <ClickableText onClick={openSeedTab}>Choose a seed</ClickableText> and hit the "Get recommendations" button above to get started.</p>)}
    </SuggestrResults>
  );
}

export default Results;
