import styled from 'styled-components'
import axios from 'axios'

const HeaderDiv = styled.div`
  height: 100px;
  width: 100%;
  background: #000;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;

  h1 {
    margin-right: 16px;
    font-family: 'Architects Daughter', cursive;
    font-size: 40px;
  }

  @media screen and (max-width: 480px) {
    h1 {
      font-size: 27px;
    }
  }
`;

const RecommendationsButton = styled.div`
  outline: 1px solid #fff;
  border-radius: 5px;
  font-size: 14px;
  display: flex;
  align-items: center;
  padding: 8px 12px;
  background-color: #333558;
  cursor: ${props => props.disabled ? 'not-allowed' : 'pointer'};
  opacity:${props => props.disabled ? '.5' : '1'};

  @media screen and (max-width: 480px) {
    font-size: 11px;
  }
`


function Header(params) {
  const {
    seedSong,
    seedArtist,
    seedGenre,
    fetchRecommendations
  } = params

  const isDisabled = () => {
    return !seedSong && !seedArtist && !seedGenre
  }

  const goHome = () => {
    document.location.href="/"
  }

  return (
    <HeaderDiv className="suggestr-header">
      <h1 onClick={goHome}>Suggestr</h1>
      <RecommendationsButton disabled={isDisabled()} onClick={fetchRecommendations}>Get recs</RecommendationsButton>
    </HeaderDiv>
  );
}

export default Header;
