import { Slider, Typography } from '@mui/material'
import ClearIcon from '@mui/icons-material/Clear';
import styled from 'styled-components'

const SongParametersWrapper = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
`
const SongParametersContainer = styled.div`
  width: 300px;

  .MuiSlider-root {
    margin-bottom: 12px;
    max-width: 250px;
  }

  .MuiSvgIcon-root {
    position: 'relative';
    left: '60px';
    margin-right: '-25px';
    cursor: 'pointer';
  }
`
const SliderLabel = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: center;
`

function SongParameters(params) {
  const {
    acousticness,
    setAcousticness,
    danceability,
    setDanceability,
    energy,
    setEnergy,
    instrumentalness,
    setInstrumentalness,
    liveness,
    setLiveness,
    loudness,
    setLoudness,
    popularity,
    setPopularity,
    speechiness,
    setSpeechiness,
    valence,
    setValence
  } = params
  
  const clearAcousticness = () => {
    setAcousticness(-1)
  }

  const clearDanceability = () => {
    setDanceability(-1)
  }

  const clearEnergy = () => {
    setEnergy(-1)
  }

  const clearInstrumentalness = () => {
    setInstrumentalness(-1)
  }

  const clearLiveness = () => {
    setLiveness(-1)
  }

  const clearLoudness = () => {
    setLoudness(-1)
  }

  const clearPopularity = () => {
    setPopularity(-1)
  }

  const clearSpeechiness = () => {
    setSpeechiness(-1)
  }

  const clearValence = () => {
    setValence(-1)
  }

  const handleAcousticnessChange = (event, value) => {
    setAcousticness(value)
  }

  const handleDanceabilityChange = (event, value) => {
    setDanceability(value)
  }

  const handleEnergyChange = (event, value) => {
    setEnergy(value)
  }

  const handleInstrumentalnessChange = (event, value) => {
    setInstrumentalness(value)
  }

  const handleLivenessChange = (event, value) => {
    setLiveness(value)
  }

  const handleLoudnessChange = (event, value) => {
    setLoudness(value)
  }

  const handlePopularityChange = (event, value) => {
    setPopularity(value)
  }

  const handleSpeechinessChange = (event, value) => {
    setSpeechiness(value)
  }

  const handleValenceChange = (event, value) => {
    setValence(value)
  }

  return (
    <SongParametersWrapper className="song-parameters">
      <SongParametersContainer>
        <SliderLabel>
          <Typography gutterBottom>Acousticness | {acousticness !== -1 ? ` ${acousticness}` : 'OFF'}</Typography>
          {acousticness !== -1 && (<ClearIcon className="clear-icon" onClick={clearAcousticness} />)}
        </SliderLabel>
        <Slider
          value={acousticness}
          aria-label="acousticness"
          defaultValue={50}
          onChange={handleAcousticnessChange}
          color={acousticness === -1 ? 'secondary' : 'primary'}
        />
        <SliderLabel>
          <Typography gutterBottom>Danceability | {danceability !== -1 ? ` ${danceability}` : 'OFF'}</Typography>
          {danceability !== -1 && (<ClearIcon className="clear-icon" onClick={clearDanceability} />)}
        </SliderLabel>
        <Slider
          value={danceability}
          aria-label="danceability"
          defaultValue={50}
          onChange={handleDanceabilityChange}
          color={danceability === -1 ? 'secondary' : 'primary'}
        />
        <SliderLabel>
          <Typography gutterBottom>Energy | {energy !== -1 ? ` ${energy}` : 'OFF'}</Typography>
          {energy !== -1 && (<ClearIcon className="clear-icon" onClick={clearEnergy} />)}
        </SliderLabel>
        <Slider
          value={energy}
          aria-label="energy"
          defaultValue={50}
          onChange={handleEnergyChange}
          color={energy === -1 ? 'secondary' : 'primary'}
        />
        <SliderLabel>
          <Typography gutterBottom>Instrumentalness | {instrumentalness !== -1 ? ` ${instrumentalness}` : 'OFF'}</Typography>
          {instrumentalness !== -1 && (<ClearIcon className="clear-icon" onClick={clearInstrumentalness} />)}
        </SliderLabel>
        <Slider
          value={instrumentalness}
          aria-label="instrumentalness"
          defaultValue={50}
          onChange={handleInstrumentalnessChange}
          color={instrumentalness === -1 ? 'secondary' : 'primary'}
        />
        <SliderLabel>
          <Typography gutterBottom>Liveness | {liveness !== -1 ? ` ${liveness}` : 'OFF'}</Typography>
          {liveness !== -1 && (<ClearIcon className="clear-icon" onClick={clearLiveness} />)}
        </SliderLabel>
        <Slider
          value={liveness}
          aria-label="liveness"
          defaultValue={50}
          onChange={handleLivenessChange}
          color={liveness === -1 ? 'secondary' : 'primary'}
        />
        <SliderLabel>
          <Typography gutterBottom>Loudness | {loudness !== -1 ? ` ${loudness}` : 'OFF'}</Typography>
          {loudness !== -1 && (<ClearIcon className="clear-icon" onClick={clearLoudness} />)}
        </SliderLabel>
        <Slider
          value={loudness}
          aria-label="loudness"
          defaultValue={50}
          onChange={handleLoudnessChange}
          color={loudness === -1 ? 'secondary' : 'primary'}
        />
        <SliderLabel>
          <Typography gutterBottom>Popularity | {popularity !== -1 ? ` ${popularity}` : 'OFF'}</Typography>
          {popularity !== -1 && (<ClearIcon className="clear-icon" onClick={clearPopularity} />)}
        </SliderLabel>
        <Slider
          value={popularity}
          aria-label="popularity"
          defaultValue={50}
          onChange={handlePopularityChange}
          color={popularity === -1 ? 'secondary' : 'primary'}
        />
        <SliderLabel>
          <Typography gutterBottom>Speechiness | {speechiness !== -1 ? ` ${speechiness}` : 'OFF'}</Typography>
          {speechiness !== -1 && (<ClearIcon className="clear-icon" onClick={clearSpeechiness} />)}
        </SliderLabel>
        <Slider
          value={speechiness}
          aria-label="speechiness"
          defaultValue={50}
          onChange={handleSpeechinessChange}
          color={speechiness === -1 ? 'secondary' : 'primary'}
        />
        <SliderLabel>
          <Typography gutterBottom>Valence | {valence !== -1 ? ` ${valence}` : 'OFF'}</Typography>
          {valence !== -1 && (<ClearIcon className="clear-icon" onClick={clearValence} />)}
        </SliderLabel>
        <Slider
          value={valence}
          aria-label="valence"
          defaultValue={50}
          onChange={handleValenceChange}
          color={valence === -1 ? 'secondary' : 'primary'}
        />
      </SongParametersContainer>
    </SongParametersWrapper>
  );
}

export default SongParameters;
