import styled from 'styled-components'
import { useEffect, useState } from 'react'
import Chip from '@mui/material/Chip';

const SelectionContainer = styled.div`
  width: 100%;
  background: #d8bfff;
  padding: 8px 0;

  .MuiChip-root {
    background-color: #fff;
    border-color: #333558;
  }
`


function Header(params) {
  const [selectionString, setSelectionString] = useState('')
  const [chips, setChips] = useState([])
  const {
    seedSong,
    seedArtist,
    seedGenre,
    acousticness,
    danceability,
    energy,
    instrumentalness,
    liveness,
    loudness,
    popularity,
    speechiness,
    valence,
    setSeedSong,
    setSeedArtist,
    setSeedGenre
  } = params

  useEffect(() => {
    const localChips = []
    if (seedSong?.name) {
      localChips.push({
        type: 'song',
        label: `Song: ${seedSong.name}`,
        id: seedSong?.id
      })
    }
    if (seedArtist?.name) {
      localChips.push({
        type: 'artist',
        label: `Artist: ${seedArtist.name}`,
        id: seedArtist?.id
      })
    }
    if (seedGenre) {
      localChips.push({
        type: 'genre',
        label: `Genre: ${seedGenre}`
      })
    }
    setChips(localChips)
    if (seedSong?.name || seedArtist?.name || seedGenre) {
      setSelectionString(`${seedSong?.name} ${seedArtist?.name || ''} ${seedGenre}`)
    }
  }, [seedSong, seedArtist, seedGenre])

  const handleDelete = (chip) => {
    if (chip.type === 'song') {
      setSeedSong('')
    }
    if (chip.type === 'artist') {
      setSeedArtist('')
    }
    if (chip.type === 'genre') {
      setSeedGenre('')
    }
    console.log('delete this!')
  }

  return (
    <SelectionContainer>{chips && chips.map((chip) => (
      <Chip label={chip.label} variant="outlined" onDelete={() => { handleDelete(chip) }} />
    ))}</SelectionContainer>
  );
}

export default Header;
