import './App.css';
import {useState, useEffect} from 'react'
import axios from 'axios'
import SeedSelection from './components/SeedSelection'
import Header from './components/Header'
import Selection from './components/Selection'
import SongParameters from './components/SongParameters'
import Results from './components/Results'

import { useTheme } from '@mui/material/styles';
import Box from '@mui/material/Box';
import MobileStepper from '@mui/material/MobileStepper';
import Paper from '@mui/material/Paper';
import Button from '@mui/material/Button';
import KeyboardArrowLeft from '@mui/icons-material/KeyboardArrowLeft';
import KeyboardArrowRight from '@mui/icons-material/KeyboardArrowRight';
import SwipeableViews from 'react-swipeable-views';
import styled from 'styled-components'

const AppContainer = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: #fff;
  font-family: 'Work Sans', sans-serif;
  height: 100%;

  .MuiBox-root {
    max-width: 100%;
    height: calc(100% - 100px);
  }

  .swipeable-views-container div:nth-child(2) {
    max-height: calc(100% - 100px);
  }
`

const ActiveLabel = styled.h2`
  text-align: center;
  width: 100%;
`

const WelcomeMessage = styled.div`
  width: 100%;
  display: flex;
  justify-content: center;
  p {
    margin: 0 10px;
    text-align: left;
    max-width: 500px;
  }
`

function App() {
  const [recommendations, setRecommendations] = useState([])
  const [activeStep, setActiveStep] = useState(0);
  const [seedArtist, setSeedArtist] = useState('')
  const [seedSong, setSeedSong] = useState('')
  const [seedGenre, setSeedGenre] = useState('')
  const [acousticness, setAcousticness] = useState(-1)
  const [danceability, setDanceability] = useState(-1)
  const [energy, setEnergy] = useState(-1)
  const [instrumentalness, setInstrumentalness] = useState(-1)
  const [liveness, setLiveness] = useState(-1)
  const [loudness, setLoudness] = useState(-1)
  const [popularity, setPopularity] = useState(-1)
  const [speechiness, setSpeechiness] = useState(-1)
  const [valence, setValence] = useState(-1)
  const [selectionIsVisible, setSelectionIsVisible] = useState(false)
  const [loading, setLoading] = useState(false)

  const theme = useTheme()

  const steps = [
    {
      label: 'Welcome',
      component: () => (<WelcomeMessage>
        <p>Get music recommendations based on Spotify's hidden parameters and unaffected by your listening history. Using Suggestr is like creating a Spotify radio station, only better.</p>
      </WelcomeMessage>)
    },
    {
      label: 'Seed Suggestr',
      component: () => (<SeedSelection 
        setSeedArtist={setSeedArtist}
        setSeedSong={setSeedSong}
        setSeedGenre={setSeedGenre}
        seedGenre={seedGenre}/>
      )
    },
    {
      label: 'Set Emotional Variables',
      component: () => (<SongParameters
        acousticness={acousticness}
        setAcousticness={setAcousticness}
        danceability={danceability}
        setDanceability={setDanceability}
        energy={energy}
        setEnergy={setEnergy}
        instrumentalness={instrumentalness}
        setInstrumentalness={setInstrumentalness}
        liveness={liveness}
        setLiveness={setLiveness}
        loudness={loudness}
        setLoudness={setLoudness}
        popularity={popularity}
        setPopularity={setPopularity}
        speechiness={speechiness}
        setSpeechiness={setSpeechiness}
        valence={valence}
        setValence={setValence}
      />)
    },
    {
      label: 'Results',
      component: () => (<Results setActiveStep={setActiveStep} recommendations={recommendations} loading={loading}/>)
    }
  ]

  const fetchRecommendations = async () => {
    setLoading(true)
    const queryParams = []
    const googleParams = {}
    if (seedSong) {
      queryParams.push({key: 'seed_tracks[]', value: seedSong.id})
      googleParams.seedSongName = seedSong?.name
      googleParams.seedSongArtist = seedSong?.artists?.[0]?.name
      googleParams.seedSongId = seedSong?.id
      googleParams.seedSongInfo = `${seedSong?.artists?.[0]?.name || ''}- ${seedSong?.name || ''}`
    }
    if (seedArtist) {
      queryParams.push({key: 'seed_artists[]', value: seedArtist.id})
      googleParams.seedArtistName = seedArtist?.name
      googleParams.seedArtistId = seedArtist?.id
    }
    if (seedGenre) {
      queryParams.push({key: 'seed_genres[]', value: seedGenre})
      googleParams.seedGenre = seedGenre
    }
    if (acousticness !== -1) {
      queryParams.push({key: 'target_acousticness', value: acousticness/100})
      googleParams.target_acousticness = acousticness/100
    }
    if (danceability !== -1) {
      queryParams.push({key: 'target_danceability', value: danceability/100})
      googleParams.target_danceability = danceability/100
    }
    if (energy !== -1) {
      queryParams.push({key: 'target_energy', value: energy/100})
      googleParams.target_energy = energy/100
    }
    if (instrumentalness !== -1) {
      queryParams.push({key: 'target_instrumentalness', value: instrumentalness/100})
      googleParams.target_instrumentalness = instrumentalness/100
    }
    if (liveness !== -1) {
      queryParams.push({key: 'target_liveness', value: liveness/100})
      googleParams.target_liveness = liveness/100
    }
    if (loudness !== -1) {
      queryParams.push({key: 'target_loudness', value: loudness/100})
      googleParams.target_loudness = loudness/100
    }
    if (popularity !== -1) {
      queryParams.push({key: 'target_popularity', value: popularity})
      googleParams.target_popularity = popularity
    }
    if (speechiness !== -1) {
      queryParams.push({key: 'target_speechiness', value: speechiness/100})
      googleParams.target_speechiness = speechiness/100
    }
    if (valence !== -1) {
      queryParams.push({key: 'target_valence', value: valence/100})
      googleParams.target_valence = valence/100
    }
    const parsedParams = queryParams.map((param) => `${param.key}=${param.value}`)

    window.gtag('event', 'get_suggestions', {...googleParams})
    
    const response = await axios.get(`https://suggestr-backend.herokuapp.com/suggestions?${parsedParams.join('&')}`)
    handleStepChange(3, true)
    setRecommendations(response?.data?.tracks)
    setLoading(false)
  }

  const hasNoSeed = () => {
    return !seedSong && !seedArtist && !seedGenre
  }

  const handleStepChange = (step) => {
    setActiveStep(step)
  }

  const maxSteps = steps.length

  const handleNext = () => {
    if (activeStep === 2 && !hasNoSeed()) {
      fetchRecommendations()
    }
    setActiveStep((prevActiveStep) => prevActiveStep + 1)
  }

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1)
  }

  useEffect(() => {
    if (seedArtist || seedGenre || seedSong) {
      setSelectionIsVisible(true)
    } else {
      setSelectionIsVisible(false)
    }
  }, [seedArtist, seedGenre, seedSong])

  return (
    <AppContainer className="App">
      <Header
        seedArtist={seedArtist}
        seedSong={seedSong}
        seedGenre={seedGenre}
        fetchRecommendations={fetchRecommendations}
      />
      { selectionIsVisible && (<Selection
          seedArtist={seedArtist}
          seedSong={seedSong}
          seedGenre={seedGenre}
          setSeedArtist={setSeedArtist}
          setSeedSong={setSeedSong}
          setSeedGenre={setSeedGenre}
      />)}
      <Box className="swipeable-views-container" sx={{ flexGrow: 1 }}>
        <Paper
          square
          elevation={0}
          sx={{
            display: 'flex',
            alignItems: 'center',
            height: 50,
            pl: 2,
            bgcolor: 'background.default',
          }}
        >
          <ActiveLabel>{steps[activeStep].label}</ActiveLabel>
        </Paper>
        <SwipeableViews
          axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
          index={activeStep}
          onChangeIndex={handleStepChange}
          enableMouseEvents
        >
          {steps.map((step, index) => (
            <div key={step.label}>
              {Math.abs(activeStep - index) <= 2 ? (
                <div>{step.component()}</div>
              ) : null}
            </div>
          ))}
        </SwipeableViews>
        <MobileStepper
          steps={maxSteps}
          position="static"
          activeStep={activeStep}
          nextButton={
            <Button
              size="small"
              onClick={handleNext}
              disabled={activeStep === maxSteps - 1}
            >
              Next
              {theme.direction === 'rtl' ? (
                <KeyboardArrowLeft />
              ) : (
                <KeyboardArrowRight />
              )}
            </Button>
          }
          backButton={
            <Button size="small" onClick={handleBack} disabled={activeStep === 0}>
              {theme.direction === 'rtl' ? (
                <KeyboardArrowRight />
              ) : (
                <KeyboardArrowLeft />
              )}
              Back
            </Button>
          }
        />
      </Box>
    </AppContainer>
  );
}

export default App;
